const customStyles = {
    content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        borderRadius: '16px',
        boxShadow: '0 2px 4px 0 rgba(0,0,0,0.1)',
        zIndex: '1000',
    }
};

const LearnPage = (props) => {
    const id = props.match?.params?.id;
    const [value, setValue] = React.useState(Number(id));
    const history = useHistory();
    const [ height, setHeight ] = React.useState(0);
    const elementRef = React.createRef();
    const tabs = [
        { value: "0", label: "" },
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" },
        { value: "6", label: "" },
    ];

    const handleChange = (event, newValue) => {
        setValue(newValue);
        history.push(`${newValue}`)
    };

    const MobileSelect = select => {
        setValue(Number(select.value));
        history.push(`${select.value}`)
    }
    function Next(){
        setValue(value+1);
        history.push(`${Number(id)+1}`)
    }
    function Prev(){
        setValue(value-1);
        history.push(`${Number(id)-1}`)
    }
    const [ content, setContent ] = React.useState([])
    
    // for modal
    const [ modalIsOpen,setIsOpen ] = React.useState(false);
    function openModal() {
        setIsOpen(true);
        
    }
    function closeModal(){
        setIsOpen(false);
    }

    React.useEffect(() => {
        fetch(markdown).then((response) => response.text()).then((text) => {
            setContent(text.split('--'))
            // setNumber(text.split('--')[5].toString().split(','))
        })

        setValue(Number(id))

        if(elementRef.current){
            setHeight(elementRef.current.offsetHeight);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);
    
    return (
        <Layout learn>
            <Seo title='- '/>
            <Wrapper ref={elementRef}>
                {/* web tab */}
                <Sticky enabled={true} top={113} bottomBoundary={height+100} activeClass='sticky' releasedClass='noSticky' className='web'>
                    <TabOption>
                        <TabTitle></TabTitle>
                        <Tabs
                            value={value}
                            className='LearnTab'
                            onChange={handleChange}
                        >
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                        </Tabs>
                    </TabOption>
                </Sticky>
                {/* mobile tab */}
                <MobileTab className='mobile'>
                    <TabOption>
                        <SelectBar>
                            <Select 
                                options={tabs} 
                                name='tabs'
                                placeholder=''
                                isSearchable={ false }
                                styles={SelectStyles}
                                defaultValue={tabs[0]}
                                value={tabs[value]}
                                onChange={MobileSelect}
                            />
                        </SelectBar>
                    </TabOption>
                </MobileTab>
                {value === 0 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[0]}
                            escapeHtml={false}
                        />
                        <BtnArea>
                            <BtnLeft></BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_02}
                        title='\n'
                        text={``}
                        BtnTitle=''
                        onClick={()=>openModal()}
                    />
                </TabArea>
                }
                {value === 1 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[1]}
                            escapeHtml={false}
                        />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_02}
                        title='\n'
                        text={``}
                        BtnTitle=''
                        onClick={()=>openModal()}
                    />
                </TabArea>
                }
                {value === 2 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[2]}
                            escapeHtml={false}
                        />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_09}
                        title=''
                        text='\n'
                        BtnTitle=''
                        onClick={()=>history.push(`/eventDetail/1`)}
                    />
                </TabArea>
                }
                {value === 3 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[3]}
                            escapeHtml={false}
                        />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_09}
                        title=''
                        text='\n'
                        BtnTitle=''
                        onClick={()=>history.push(`/eventDetail/1`)}
                    />
                </TabArea>
                }
                {value === 4 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[4]}
                            escapeHtml={false}
                        />
                        <CellNumber bg='#E65C52' num='1' text='' />
                        <CellNumber bg='#FF9F47' num='2' text='' />
                        <CellNumber bg='#FFC900' num='3' text='' />
                        <CellNumber bg='#59C08E' num='4' text='' />
                        <CellNumber bg='#47AAD3' num='5' text='' />
                        <CellNumber bg='#377AF1' num='6' text='' />
                        <CellNumber bg='#B780F2' num='7' text='' />
                        <CellNumber bg='#692FA6' num='8' text='' />
                        <CellNumber bg='#6D562D' num='9' text='' />
                        <CellNumber bg='#B0B0B0' num='0' text='' />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_09}
                        title=''
                        text='\n'
                        BtnTitle=''
                        onClick={()=>history.push(`/eventDetail/1`)}
                    />
                </TabArea>
                }
                {value === 5 &&
                <TabArea>
                    <CardArticle>
                        <ReactMarkdown
                            source={content[5]}
                            escapeHtml={false}
                        />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight onClick={Next}>
                                <div><p></p></div>
                                <img src={Images.arrow_right} alt='arrow_right'/>
                            </BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_09}
                        title=''
                        text='\n'
                        BtnTitle=''
                        onClick={()=>history.push(`/eventDetail/1`)}
                    />
                </TabArea>
                }
                {value === 6 &&
                <TabArea>
                    <CardArticle>
                        <CellToggle 
                            title='?'
                            text='8~()'
                        />
                        <CellToggle 
                            title='?'
                            text=''
                        />
                        <CellToggle 
                            title='?'
                            text='\n\n'
                        />
                        <CellToggle 
                            title='?'
                            text='......~'
                        />
                        <CellToggle 
                            title='?'
                            text='~()'
                        />
                        <CellToggle 
                            title=''
                            text='~()\n'
                        />
                        <CellToggle 
                            title=''
                            text=''
                        />
                        <CellToggle 
                            title='?'
                            text='~'
                        />
                        <CellToggle 
                            title='?'
                            text='~'
                        />
                        <CellToggle 
                            title='?'
                            text='~\n()'
                        />
                        <BtnArea>
                            <BtnLeft onClick={Prev}>
                                <img src={Images.arrow_left} alt='arrow_left'/>
                                <div><p></p></div>
                            </BtnLeft>
                            <BtnRight></BtnRight>
                        </BtnArea>
                    </CardArticle>
                    <CardBannerS 
                        bg={Images.Event_09}
                        title=''
                        text='\n'
                        BtnTitle=''
                        onClick={()=>history.push(`/eventDetail/1`)}
                    />
                </TabArea>
                }
            </Wrapper>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Close onClick={closeModal}>
                    <img src={Images.close} alt='' />
                </Close>
                <Calculator />
            </Modal>
        </Layout>
    );
}

export default LearnPage;