import React from 'react';
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { FormTitleDiv, SelectBar, CheckBar } from '../../components'
import { Fonts, SelectStyles } from '../../themes'
import Select from 'react-select'

const ContentArea = styled.div`
    position: relative;
    width: 100%;
    box-sizing: border-box;
    display: grid;
    justify-items: center;
    .selectBar{
        max-width: 344px;
        margin: 0 auto;
    }
    .checkBar{
        margin: 0 auto;
        max-width: 250px;
        padding-bottom: 30px;
    }
`
const TextArea = styled.div`
    padding: 22px 24px 33px;
`

const Text = styled(Fonts.P_16())`
    text-align: center;
    .br{
        margin-bottom: 20px;
    }
`

const Refund = (props) => {
    const [ check, setCheck ] = React.useState(false);
    const reasons = [
        { value: "0", label: "" },
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" },
    ];
    return (
        <FormTitleDiv title='' close={props.close} Submit={props.submit}>
            <ContentArea>
                <SelectBar className='full' inputTitle=''>
                    <Select 
                        options={reasons} 
                        name='reasons'
                        isSearchable={ false }
                        placeholder=''
                        styles={SelectStyles}
                    />
                </SelectBar>
                <TextArea>
                    <Text></Text>
                    <Text className='br'> <Link to='mailto:service@rainbow.com' target='_blank'>service@rainbow.com</Link></Text>
                    <Text ></Text>
                    <Text>:</Text>
                </TextArea>
                <CheckBar className='black'>
                    <input type="checkbox" name="time" id='time' onChange={()=>setCheck(!check)} />
                    <span htmlFor="time" className='bold'> </span>
                </CheckBar>
            </ContentArea>
        </FormTitleDiv>
    );
}

export default Refund;