import React from 'react';
import { Seo, Layout, CardBanner, TitleArea, CardA, CardB, GridCol3, Calculator, ContactModal, CardBannerS  } from '../components'
import styled from 'styled-components'
import { Images, Metrics } from '../themes'
import Modal from 'react-modal'
import { useHistory } from "react-router-dom";

const Wrapper = styled.div`
    position: relative;
    padding: 0 16px;
`
const Close = styled.div`
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10000;
    width: 24px;
    height: 24px;
    filter: invert(1);
    &:hover{
        cursor: pointer;
    }
`
const customStyles = {
    content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        borderRadius: '16px',
        boxShadow: '0 2px 4px 0 rgba(0,0,0,0.1)',
        zIndex: '10000',
    }
};

const Img = styled.div`
    width: 80%;
    margin: 0 auto;
    position: relative;
    img{
        width: 100%;
    }

    @media (max-width: ${Metrics.tablet}){
        width: 100%;
      }

    @media (max-width: ${Metrics.mobile}){
        width: 100%;
      }
`

const IndexPage = (props) => {
    const history = useHistory();
    const [modalIsOpen,setIsOpen] = React.useState(false);
    function openModal() {
        setIsOpen(true);
    }
    function closeModal(){
        setIsOpen(false);
    }

    const [ ContactIsOpen, setContact ] = React.useState(false);

    function openContact() {
        setContact(true);
    }
    function closeContact(){
        setContact(false);
    }

    const [width, setWidth] = React.useState(window.innerWidth);
    const updateWidthAndHeight = () => { setWidth(window.innerWidth); } 

    React.useEffect(() => {
        if(history.location.hash==='#media'){
            if(width>568){
                window.scrollTo({ 
                    top: 1500, 
                    behavior: "smooth" 
                });
            }else{
                window.scrollTo({ 
                    top: 2200, 
                    behavior: "smooth" 
                });
            }
        }
    })

    window.addEventListener("resize", updateWidthAndHeight);
    return (
        <Layout>
            <Seo title=''/>
            <Wrapper>
                <CardBanner
                    bg={Images.cover}
                    title={width>568 ? `\\n\\n - ` : ' - '}
                    text={width>568 ? '\\n\\n' : ''}
                    BtnTitle=''
                    onClick={()=>history.push(`/learn/0`)}
                />
                <TitleArea
                    title=' '
                    text=''
                />

                <GridCol3>
                    <CardA
                        title='' text=''
                        img={Images.Reservation_01}
                        onClick={openContact}
                        linkText=''
                    />
                    <CardA
                        title=''
                        text=''
                        img={Images.Reservation_02}
                        onClick={openContact}
                        linkText=''
                    />
                    <CardA
                        title=''
                        text=''
                        img={Images.Reservation_03}
                        onClick={openContact}
                        linkText=''
                    />
                </GridCol3>
                <TitleArea
                    title=''
                    link='/event/0'
                    linkText=''
                />
                <Img>
                    {width > 568 
                        ? <img src={Images.Class} alt='' />
                        : <img src={Images.class_mobile} alt='' />
                    }
                </Img>
                <TitleArea
                    title=''
                    text=''
                    id='media'
                />
                <GridCol3>
                    <CardB
                        img={Images.News_01}
                        title=''
                        source=''
                        date='2017-07-19'
                        href='https://www.wealth.com.tw/home/articles/11464'
                    />
                    <CardB
                        img={Images.News_02}
                        title=''
                        source=''
                        date='2020-09-10'
                        href='https://ek21.com/news/202685/'
                    />
                    <CardB
                        img={Images.News_03}
                        title=' 9 '
                        source='ETtoday'
                        date='2017-06-21'
                        href='https://www.ettoday.net/news/20170621/949721.htm'
                    />
                </GridCol3>
                <CardBannerS
                    className='center'
                    bg={Images.banner_02}
                    title=''
                    text=''
                    BtnTitle='' 
                    onClick={()=>openModal()}
                />
            </Wrapper>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Close onClick={closeModal}>
                    <img src={Images.close} alt='' />
                </Close>
                <Calculator />
            </Modal>
            <Modal
                isOpen={ContactIsOpen}
                onRequestClose={closeContact}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <ContactModal 
                    onClick={closeContact}
                />
            </Modal>
        </Layout>
    );
}

export default IndexPage;