import React from 'react';
import { Seo, Layout, CardArticleWithTitle, CellIconTitle, 
        CellAccountLevel, CellBDEmpty, CellTable, CardArticleWithTab,
        CardTicket,CardTicketDone, EditProfileModal, EditRecommendModal,
        RefundModal,SelectBar
    } from '../components'
import styled from 'styled-components'
import { Colors, Images, Fonts, Metrics, SelectStyles  } from '../themes'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Modal from 'react-modal'
import { useHistory } from "react-router-dom";
import Select from 'react-select'
import '../css/modalWhite.css'
import Sticky from 'react-stickynode';

const FormStyles = {
    content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        borderRadius: '16px',
        boxShadow: '0 2px 4px 0 rgba(0,0,0,0.1)',
        zIndex: '1000',
        width: '100%',
        maxWidth: '640px',
        padding: '0'
    }
};

const Wrapper = styled.div`
    padding: 24px 16px;
    display: grid;
    grid-template-columns: 220px auto;
    max-width: 1000px;
    margin: 0 auto;
    @media (max-width: ${Metrics.mobile}){
        grid-template-columns: 100%;
        padding: 0;
    }
`
const MobileTab = styled.div`
`
const TabOption = styled.div`
    position: relative;
    max-width: 152px;
    margin: 0 16px;
    .LearnTab{
        max-width: 152px;
        margin: 0 auto;
        box-sizing: border-box;
    }
    .MuiTabs-flexContainer{
        display: grid;
        justify-items: start;
    }
    .MuiTabs-indicator{
        display: none;
    }
    
    .MuiButtonBase-root{
        text-align: left;
        margin: 2px 0;
        font-size: 16px;
        font-weight: 600;
        line-height: 22px;
    }
    .Mui-selected{
        color: #3578F1;
    }
    .MuiTab-root{
        min-width: inherit;
        min-height: inherit;
    }
    @media (max-width: ${Metrics.mobile}){
        max-width: inherit;
        margin: 0;
    }
`
const TabTitle = styled(Fonts.h4())`
    padding: 10px;
`
const TabArea = styled.div`
    margin: 0 16px;
`

const Title = styled(Fonts.h5())`
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    margin: 8px 0 8px;
    &:first-child{
        margin-top: 0;
    }
`
const Text = styled(Fonts.P_16())`
    ${'' /* margin-top: 8px; */}
    color:${Colors.G3};
    white-space: pre-wrap;
    &.br{
        padding-bottom: 20px;
    }
    &:last-child{
        ${'' /* padding-bottom: 20px; */}
    }
`
// const Info = styled(Fonts.info())`
//     color: #9B9B9B;
//     &.br{
//         padding-bottom: 20px;
//     }
// `
const TextArea = styled.div`
    padding: 16px 24px 28px;
`

const AccountPage = (props) => {
    const id = props.match?.params?.id;
    const [ value, setValue ] = React.useState(0);
    const history = useHistory();
    const [ valueInCard, setValueInCard ] = React.useState(0);
    const [ birthday ] = React.useState('1986/04/08');
    const [ height, setHeight ] = React.useState(0);
    const elementRef = React.createRef();
    const tabs = [
        { value: "0", label: "" },
        { value: "1", label: "" },
        { value: "2", label: "" },
        
    ];
    const handleChange = (event, newValue) => {
        setValue(newValue);
        history.push(`${newValue}`)
    };
    const handleChangeInCard = (event, newValue) => {
        setValueInCard(newValue);
    };
    const MobileSelect = select => {
        setValue(Number(select.value));
        history.push(`${select.value}`)
    }
    //ModalProfile
    const [ProfileIsOpen,setProfileIsOpen] = React.useState(false);
    function openProfile() {
        setProfileIsOpen(true);
    }
    function closeProfile(){
        setProfileIsOpen(false);
    }
    //ModalRecom
    const [ RecomIsOpen, setRecomIsOpen ] = React.useState(false);
    function openRecom() {
        setRecomIsOpen(true);
    }
    function closeRecom(){
        setRecomIsOpen(false);
    }
    //ModalRefund
    const [ RefundIsOpen, setRefundIsOpen ] = React.useState(false);
    function openRefund() {
        setRefundIsOpen(true);
    }
    function closeRefund(){
        setRefundIsOpen(false);
    }
    React.useEffect(() => {
        setValue(Number(id))

        if(elementRef.current){
            setHeight(elementRef.current.offsetHeight);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);
    return (
        <Layout learn>
            <Seo title='- '/>
            <Wrapper ref={elementRef}>
                {/* web tab */}
                <Sticky enabled={true} top={113} bottomBoundary={height+100} activeClass='sticky' releasedClass='noSticky' className='web'>
                    <TabOption>
                        <TabTitle></TabTitle>
                        <Tabs
                            value={value}
                            className='LearnTab'
                            onChange={handleChange}
                        >
                            <Tab label='' />
                            <Tab label='' />
                            <Tab label='' />
                        </Tabs>
                    </TabOption>
                </Sticky>
                {/* mobile tab */}
                <MobileTab className='mobile'>
                    <TabOption>
                        <SelectBar>
                            <Select 
                                options={tabs} 
                                name='tabs'
                                placeholder=''
                                isSearchable={ false }
                                styles={SelectStyles}
                                defaultValue={tabs[0]}
                                value={tabs[value]}
                                onChange={MobileSelect}
                            />
                        </SelectBar>
                    </TabOption>
                </MobileTab>
                {value === 0 &&
                <TabArea>
                    <CardArticleWithTitle bigTitle=''>
                        <CellIconTitle 
                            icon={Images.star}
                            alt='star-icon'
                            title=''
                        />
                        <CellAccountLevel 
                            img={Images.ceo}
                            name='Shelly Lin'
                            title='Lv.1  '
                        />
                        <CellIconTitle 
                            icon={Images.birthday}
                            alt='birthday-icon'
                            title=''
                        />
                        {birthday 
                            ? <>
                                <Title>1986/04/08</Title>
                                <Text className='br'> ()</Text>
                                <Text className='br'> ()</Text>
                                <Text className='br'></Text>
                                <Text className='br'></Text>
                            </>
                            : <CellBDEmpty 
                                img={Images.ask}
                                title=''
                                text=''
                                btnTitle=''
                            />
                        }
                        
                    </CardArticleWithTitle>
                </TabArea>
                }
                {value === 1 &&
                <TabArea>
                    <CardArticleWithTitle bigTitle=''>
                        <CellIconTitle icon={Images.phone} alt='phone-icon'
                            title=''
                        />
                        <CellTable 
                            title=''
                            text=' Facebook '
                            logout
                        />
                        <CellIconTitle icon={Images.medal} alt='medal-icon'
                            title=''
                        />
                        <CellTable title='' text='Lv.1 ' />
                        <CellTable title='' text='' />
                        <CellTable title='' text='' />
                        <CellTable title='' text='2021 / 06 / 22 ' />
                        <CellIconTitle icon={Images.profile} alt='profile-icon'
                            title=''
                            onClick={openProfile}
                        />
                        <CellTable title='' text='' />
                        <CellTable title='' text='' />
                        <CellTable title='' text='1990/08/09' />
                        <CellTable title='' text='0911345668' />
                        <CellTable title='' text='shelly@uiuxcafe.com' />
                        <CellIconTitle icon={Images.name} alt='name-icon'
                            title=''
                            onClick={openRecom}
                        />
                        <Text></Text>
                    </CardArticleWithTitle>
                 
                </TabArea>
                }
                {value === 2 &&
                <TabArea>
                    <CardArticleWithTab bigTitle='' >
                        <Tabs
                            value={valueInCard}
                            className='InCardTab'
                            onChange={handleChangeInCard}
                        >
                            <Tab label='()' />
                            <Tab label='()' />
                            <Tab label='()' />
                            <Tab label='()' />
                        </Tabs>
                        {valueInCard===0 &&
                            <TextArea>
                                <CardTicket 
                                    title=''
                                    price='NT$ 1,000'
                                    control=''
                                    status=''
                                    onClick={openRefund}
                                />
                                <CardTicket 
                                    title=''
                                    price='NT$ 1,000'
                                    control=''
                                    status=''
                                    onClick={openRefund}
                                />
                            </TextArea>
                        }
                        {valueInCard===1 &&
                        <TextArea>
                            <CardTicket 
                                title=''
                                price='NT$ 1,000'
                                control=''
                                status='pay'
                            />
                        </TextArea>
                        }
                        {valueInCard===2 &&
                        <TextArea>
                            <CardTicket 
                                title=''
                                price='NT$ 1,000'
                                control=''
                                status='resign'
                            />
                        </TextArea>
                        }
                        {valueInCard===3 &&
                        <TextArea>
                            <CardTicketDone 
                                title=' ( 10 ' 
                                price='NT$ 6,000' 
                            />
                            <CardTicketDone 
                                title='' 
                                price='NT$ 3,000' 
                            />
                            <CardTicketDone 
                                title='' 
                                price='NT$ 7,000' 
                            />
                        </TextArea>
                        }
                    </CardArticleWithTab>
                </TabArea>
                }
            </Wrapper>
            <Modal
                isOpen={ProfileIsOpen}
                onRequestClose={closeProfile}
                style={FormStyles}
                contentLabel="Example Modal"
            >
                <EditProfileModal
                    onClick={closeProfile}
                    close={closeProfile}
                    submit={closeProfile}
                />
            </Modal>
            <Modal
                isOpen={RecomIsOpen}
                onRequestClose={closeRecom}
                style={FormStyles}
                contentLabel="Recommend Modal"
            >
                <EditRecommendModal
                    onClick={closeRecom}
                    close={closeRecom}
                    submit={closeRecom}
                />
            </Modal>
            <Modal
                isOpen={RefundIsOpen}
                onRequestClose={closeRefund}
                style={FormStyles}
                contentLabel="Refund Modal"
            >
                <RefundModal
                    onClick={closeRefund}
                    close={closeRefund}
                    submit={closeRefund}
                />
            </Modal>
        </Layout>
    );
}

export default AccountPage;