import React, { useState, useRef } from 'react';
import styled from 'styled-components'
import { Images,Colors,Fonts } from '../themes'
import { CalculateBtn, InputDateBar, CheckBar } from '../components'
import Slider from "react-slick"
import { DatePicker, Space, TimePicker } from 'antd';
import {isMobileOnly} from 'react-device-detect';
var moment = require('moment');

const CheckOne = [
    '',
    '',
    '',
    '',
    '',
    '',
    ''
]
const CheckNight = [
    '',
    '',
    '',
    '',
    '',
    ''
]
const CheckZero = [
    '',
    '',
    '',
    '',
    '',
    ''
]
const NumberOptions = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
]
const SoulLVOptions = [
    {title:'',text:''},
    {title:'',text:'()()() '},
    {title:'',text:''},
    {title:'',text:''},
    {title:'',text:''},
    {title:'',text:''},
    {title:'',text:''},    
]

const Wrapper = styled.div`
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    max-width: 568px;
    max-height: 683px;
    min-height: 400px;
    padding-bottom: 80px;
    .slick-slider{
        width: 100%;
    }
    .slick-next, .slick-prev{
        position: relative;
        display: flex;
        top: 0;
        left: 50%;
        background: #000;
    }
    @media (max-width: 425px){
        max-height: inherit;
        display: flex;
        align-items: center;
    }
`
const Content = styled.div`
    position: relative;
    width: 100%;
    display: grid !important;
    justify-items: center;
    padding: 46px 24px 20px;
    width: 90%;
    box-sizing: border-box;
    .submit{
        margin-top: 24px;
        position: relative;
        z-index: 10000;
    }
    .checkBar{
        justify-self: center;
    }
`
// const Img = styled.img`
//     margin: 0 auto;
//     padding-bottom: 20px;
// `
const Title = styled(Fonts.h3())`
    color: ${Colors.W100};
    text-align: center;
    margin: 0 !important;
    padding-bottom: 12px;
    text-align: center;
`
const Text = styled.p`
    color: ${Colors.W100};
    font-size: 16px;
    margin: 0;
    letter-spacing: 0;
    line-height: 22px;
    text-align: center;
`
const BtnArea = styled.div`
    display: flex;
    justify-content: center;
    position: absolute;
    width: 100%;
    bottom: 0px !important;
    z-index: 1000;
    margin-bottom: 16px;
    button{
        margin: 0 4px;
    }
`

const Calculator = (props) => {
    const [ date, setDate ] = useState('');
    const [ time, setTime ] = useState('');
    const [ check, setCheck ] = useState(false);
    const [ alert, setAlert ] = useState(false)
    const NumberList = date.split("").filter(i=>i!=='-')
    let BackNumbers = sum(NumberList.map(Number)).toString().split("")
    let SecondBackNumbers = ''
    let MainNmber = ''
    let NumbersCheck =''
    let SoulLV 
    const ResultSlider = useRef();
    const [ sliderSettings ] = useState({
        infinite: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        speed: 300,
        cssEase: `linear`,
        adaptiveHeight: true,
        draggable: false,
        swipe: false,
    })
    const [ index, setIndex ] = useState(0);

    const gotoNext = () => {
        ResultSlider.current.slickNext()
        setIndex(index+1)
    }
    const gotoPrev = () => {
        ResultSlider.current.slickPrev()
        setIndex(index-1)
    }
    const Restart = ()=> {
        ResultSlider.current.slickGoTo(0)
        setIndex(0)
    }

    function onChange(e, dateString) {
        setAlert(false)
        if(isMobileOnly){
            setDate(e.target.value)
        }else{
            setDate(dateString)
        }
        
    }
    function Submit(){
        if(date){
            setAlert(false)
            ResultSlider.current.slickNext()
            setIndex(index+1)
        }else{
            setAlert(true)
        }
    }

    function sum(arr) {
        var len = arr.length;
        if(len === 0){
            return 0;
        } else if (len === 1){
            return arr[0];
        } else {
            return arr[0] + sum(arr.slice(1));
        }
    }

    if(sum(BackNumbers.map(Number)).toString().split("").length>1){
        SecondBackNumbers = sum(BackNumbers.map(Number)).toString().split("")
        MainNmber =  sum(SecondBackNumbers.map(Number)).toString()
    }else{
        MainNmber = sum(BackNumbers.map(Number)).toString()
    }

    function CheckNum(){
        let NumberCheck =[]
        for(let t = 0;t<10;t++){
            if(t===0 || t===1 || t===9){
                NumberCheck.push('')
            }else{
                if(NumberList.filter(i=>i===`${t}`).length > 0){
                    NumberCheck.push(`${t}`)
                }else{
                    NumberCheck.push('')
                }
            }
        }
        NumbersCheck = NumberCheck
    }

    function CheckSoul(){
        if(SecondBackNumbers.length>1){
            if(NumberList.filter(i=>i===MainNmber).length>0){
                if(NumberList.filter(i=>i===BackNumbers[0]).length>0 && NumberList.filter(i=>i===BackNumbers[1]).length>0
                    && NumberList.filter(i=>i===SecondBackNumbers[0]).length>0 && NumberList.filter(i=>i===SecondBackNumbers[1]).length>0
                ){
                    let NumberCheck =[]
                    for(let t = 0;t<10;t++){
                        if(NumberList.filter(i=>i===`${t}`).length > 2){
                            NumberCheck.push('6')
                        }else{
                            NumberCheck.push('7')
                        }
                    }
                    if((NumberCheck.filter(i=>i==="6").length > 0)){
                        console.log(6)
                        SoulLV=6
                    }else{
                        console.log(7)
                        SoulLV=7
                    }
                }else if(NumberList.filter(i=>i!==BackNumbers[0]) && NumberList.filter(i=>i!==BackNumbers[1]) 
                    && NumberList.filter(i=>i!==SecondBackNumbers[0]) && NumberList.filter(i=>i!==SecondBackNumbers[1])
                ){
                    SoulLV=4
                }else{
                    SoulLV=5
                }
            }else{
                if(NumberList.filter(i=>i===BackNumbers[0]).length>0 && NumberList.filter(i=>i===BackNumbers[1]).length>0
                    && NumberList.filter(i=>i===SecondBackNumbers[0]).length>0 && NumberList.filter(i=>i===SecondBackNumbers[1]).length>0
                ){
                    SoulLV=3
                }else if(NumberList.filter(i=>i!==BackNumbers[0]).length===NumberList.length && NumberList.filter(i=>i!==BackNumbers[1]).length===NumberList.length 
                    && NumberList.filter(i=>i!==SecondBackNumbers[0]).length===NumberList.length && NumberList.filter(i=>i!==SecondBackNumbers[1]).length===NumberList.length
                ){
                    SoulLV=1
                }else{
                    SoulLV=2
                }
            }
        }else{
            if(NumberList.filter(i=>i===MainNmber).length>0){
                if(NumberList.filter(i=>i===BackNumbers[0]).length>0 && NumberList.filter(i=>i===BackNumbers[1]).length>0){
                    let NumberCheck =[]
                    for(let t = 0;t<10;t++){
                        if(NumberList.filter(i=>i===`${t}`).length > 2){
                            NumberCheck.push('6')
                        }else{
                            NumberCheck.push('7')
                        }
                    }
                    if((NumberCheck.filter(i=>i==="6").length > 0)){
                        SoulLV=6
                    }else{
                        SoulLV=7
                    }
                }else if(NumberList.filter(i=>i!==BackNumbers[0]).length===NumberList.length && NumberList.filter(i=>i!==BackNumbers[1]).length===NumberList.length){
                    SoulLV=4
                }else{
                    SoulLV=5
                }
            }else{
                if(NumberList.filter(i=>i===BackNumbers[0]).length>0 && NumberList.filter(i=>i===BackNumbers[1]).length>0){
                    SoulLV=3
                }else if(NumberList.filter(i=>i!==BackNumbers[0]).length===NumberList.length && NumberList.filter(i=>i!==BackNumbers[1]).length===NumberList.length){
                    SoulLV=1
                }else{
                    SoulLV=2
                }
            }
        }
        
    }

    // console.log(BackNumbers)
    // console.log(SecondBackNumbers)
    // console.log(MainNmber)
    CheckNum()
    CheckSoul()
    return (
        <Wrapper>
            <Slider {...sliderSettings} ref={ResultSlider}>
                <Content>
                    <Title></Title>
                    {isMobileOnly
                        ? <InputDateBar inputTitle=''>
                            <input id='date' className={date ? 'no-placeholder': ''} type='date' placeholder='2020-01-01' value={date} onChange={e=>onChange(e)} />
                            <img src={Images.duedate_w} alt='duedate' />
                        </InputDateBar>
                        : <InputDateBar inputTitle=''>
                            <Space direction="vertical">
                                <DatePicker className={alert ? 'alert' : ''} placeholder='2020-01-01' onChange={onChange} require inputReadOnly />
                            </Space>
                        </InputDateBar>
                    }
                    {isMobileOnly
                        ? <InputDateBar inputTitle=''>
                            <input className={`${check ? 'disable' : ''} ${time ? 'no-placeholder' : ''}`} type='time' value={time}  onChange={e=>setTime(e.target.value)} placeholder="00:00"/>
                            <img src={Images.clock_w} alt='clock' />
                        </InputDateBar>
                        : <InputDateBar inputTitle=''>
                            <TimePicker
                                defaultValue={moment('00:00', 'HH:mm')}
                                format={'HH:mm'}
                                allowClear={false}
                                disabled={check ? true : false}
                                inputReadOnly
                            />
                        </InputDateBar>
                    }
                    <CheckBar>
                        <input type="checkbox" name="time" id='time' onChange={()=>setCheck(!check)} />
                        <span htmlFor="time"></span>
                    </CheckBar>
                </Content>
                <Content>
                    <Title>{date.replace('-','/').replace('-','/')}</Title>
                    <Text></Text>
                    <Text>{CheckOne[NumberList.filter(i=>i==='1').length]}</Text>
                </Content>
                <Content>
                    <Title>{date.replace('-','/').replace('-','/')}</Title>
                    <Text>{CheckZero[NumberList.filter(i=>i==='9').length]}</Text>
                </Content>
                <Content>
                    <Title>{date.replace('-','/').replace('-','/')}</Title>
                    <Text>{CheckNight[NumberList.filter(i=>i==='0').length]}</Text>
                </Content>
                <Content>
                    <Title>{date.replace('-','/').replace('-','/')}</Title>
                    <Text>{SoulLVOptions[SoulLV-1].text}</Text>
                </Content>
                <Content>
                    <Title>{date.replace('-','/').replace('-','/')}</Title>
                    <Text>
                        {NumbersCheck.filter(i=>i!=='').map((i, index) => (
                            NumberOptions[NumbersCheck.filter(i=>i!=='')[index]]
                        ))}
                        
                    </Text>
                </Content>
            </Slider>
                {index===0 &&
                    <BtnArea>
                        <CalculateBtn
                            className={date ? 'submit calculator lg' : 'calculator submit disable lg'}
                            btnName=''
                            onClick={()=> Submit()}
                        />
                    </BtnArea>
                }
                {index===1 &&
                    <BtnArea>
                        <CalculateBtn 
                            className="next calculator lg"
                            btnName=''
                            onClick={()=>gotoNext()}
                        />
                    </BtnArea>
                }
                {index>1 && index<5 &&
                    <BtnArea>
                        <CalculateBtn
                            className="prev calculator lg"
                            btnName=''
                            onClick={()=>gotoPrev()}
                        />
                        <CalculateBtn 
                            className="next calculator lg"
                            btnName=''
                            onClick={()=>gotoNext()}
                        />
                    </BtnArea>
                }
                {index === 5 &&
                    <BtnArea>
                        <CalculateBtn
                            className="prev calculator lg"
                            btnName=''
                            onClick={()=>gotoPrev()}
                        />
                        <CalculateBtn
                            className='calculator lg'
                            btnName=''
                            onClick={Restart}
                        />
                    </BtnArea>
                }
        </Wrapper>
    );
}

export default Calculator;
