const Wrapper = styled.div`
    padding: 0 16px;
    .slick-slider{
        width: 90%;
        max-width: 1140px;
        margin: 0 auto;
        padding: 0 32px;
    }
    .slick-track{
        padding: 30px 0;
    }
    .slick-disabled{
        opacity: 0 !important;
    }
    .slick-arrow::before{
        display: none;
    }
    @media (max-width: 568px){
        .slick-slider{
            width: 100%;
            padding: 0 0 0 16px;
        }
        .slick-arrow{
            display: none !important;
        } 
    }
`   
const Close = styled.div`
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10000;
    width: 24px;
    height: 24px;
    filter: invert(1);
    &:hover{
        cursor: pointer;
    }
`
